<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="container-fluid mt-4">
      <!-- add Product Btn -->
      <div class="text-right mb-3">
        <button
          @click="openModal('new')"
          type="button"
          class="btn btn-sm btn-outline-success"
          data-toggle="modal"
          data-target="#addModal"
        >
          <i class="fas fa-plus mr-2"></i>
        </button>
      </div>
      <!-- Table -->
      <table class="table table-striped table-hover">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col" class="text-center"></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item,key) in products" :key="item.id">
            <th scope="row">{{ key+1 }}</th>
            <td>
              <img :src="item.imageUrl" class="smallImg" alt />
            </td>
            <td>{{ item.category }}</td>
            <td>{{ item.title }}</td>
            <td>{{ item.origin_price }}</td>
            <td>{{ item.price }}</td>
            <td class="text-center">
              <i class="far fa-circle text-success" v-if="item.enabled"></i>
              <i class="fas fa-minus-circle text-muted" v-else></i>
            </td>
            <td class="text-center">
              <div class="btn-group">
                <!--  Btn -->
                <button
                  @click="openModal('edit', item)"
                  type="button"
                  class="btn btn-outline-secondary btn-sm"
                  data-toggle="modal"
                  data-target="#addModal"
                >
                  <i class="fas fa-pen"></i>
                </button>
                <!--  Btn -->
                <button
                  @click="openModal('dele', item)"
                  type="button"
                  class="btn btn-outline-danger btn-sm"
                  data-toggle="modal"
                  data-target="#deleModal"
                >
                  <i class="fas fa-trash-alt"></i>
                </button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
      <!-- pagination  -->
      <pagination :pages="pagination" @emit-pages="getProducts"/>
      <!-- add Modal -->
      <div
        class="modal fade bd-example-modal-xl"
        id="addModal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="addModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog modal-xl" role="document">
          <div class="modal-content">
            <div class="modal-header bg-dark text-white">
              <h5 class="modal-title" id="addModalLabel"></h5>
              <button
                type="button"
                class="close text-white"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <div class="container-fluid">
                <div class="row">
                  <div class="col-md-4">
                    <div class="form-group">
                      <label for="imageUrl"></label>
                      <input
                        id="imageUrl"
                        v-model="tempProduct.imageUrl[0]"
                        type="text"
                        class="form-control"
                        placeholder=""
                      />
                      <img :src="tempProduct.imageUrl[0]" class="img-fluid mt-3" />
                    </div>
                  </div>
                  <div class="col-md-8">
                    <form>
                      <div class="form-group">
                        <label for="inputItemName"></label>
                        <input
                          type="text"
                          class="form-control"
                          id="inputItemName"
                          placeholder=""
                          v-model="tempProduct.title"
                        />
                      </div>
                      <div class="form-row">
                        <div class="form-group col-md-6">
                          <label for="inputCategory"></label>
                          <input
                            type="text"
                            class="form-control"
                            id="inputCategory"
                            placeholder=""
                            v-model="tempProduct.category"
                          />
                        </div>
                        <div class="form-group col-md-6">
                          <label for="inputUnit"></label>
                          <input
                            type="text"
                            class="form-control"
                            id="inputUnit"
                            placeholder=""
                            v-model="tempProduct.unit"
                          />
                        </div>
                      </div>
                      <div class="form-row">
                        <div class="form-group col-md-6">
                          <label for="inputOriginPrice"></label>
                          <input
                            type="number"
                            class="form-control"
                            id="inputOriginPrice"
                            placeholder=""
                            v-model="tempProduct.origin_price"
                          />
                        </div>
                        <div class="form-group col-md-6">
                          <label for="inputPrice"></label>
                          <input
                            type="number"
                            class="form-control"
                            id="inputPrice"
                            placeholder=""
                            v-model="tempProduct.price"
                          />
                        </div>
                      </div>
                      <hr />
                      <div class="form-group">
                        <label for="inputDescription"></label>
                        <textarea
                          class="form-control"
                          id="inputDescription"
                          rows="2"
                          placeholder=""
                          v-model="tempProduct.description"
                        ></textarea>
                      </div>
                      <div class="form-group">
                        <label for="inputContent"></label>
                        <vue-editor
                          v-model="tempProduct.content"
                          id="inputContent">
                        </vue-editor>
                      </div>
                      <div class="form-group">
                        <div class="form-check">
                          <input
                            class="form-check-input formCheck"
                            type="checkbox"
                            id="gridCheck"
                            v-model="tempProduct.enabled"
                          />
                          <label class="form-check-label" for="gridCheck"></label>
                        </div>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
            <hr class="m-0" />
            <div class="modal-footer">
              <button type="button" class="btn btn-outline-secondary btn-sm"
                data-dismiss="modal"></button>
              <button @click="updateProduct" type="button"
                class="btn btn-primary btn-sm"></button>
            </div>
          </div>
        </div>
      </div>
      <!-- dele Modal -->
      <div
        class="modal fade"
        id="deleModal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="deleModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header bg-danger text-white">
              <h5 class="modal-title" id="deleModalLabel"></h5>
              <button
                type="button"
                class="close text-white"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              
              <strong class="text-danger">{{ tempProduct.title }}</strong> ()
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-sm btn-outline-secondary"
                data-dismiss="modal"></button>
              <button type="button" class="btn btn-sm btn-danger"
                @click="deleProduct(tempProduct.id)"></button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* global $ */
import Pagination from '@/components/Pagination.vue';
import { VueEditor } from 'vue2-editor';

export default {
  name: 'Products',
  components: {
    Pagination,
    VueEditor,
  },
  data() {
    return {
      products: [],
      pagination: {},
      // token: '',
      tempProduct: { // input 
        imageUrl: [],
      },
      isNew: false,
      isLoading: false,
    };
  },
  props: ['token'],
  created() {
    //  token Dashboard 
    // this.token = document.cookie.replace(/(?:(?:^|.*;\s*)myToken\s*=\s*([^;]*).*$)|^.*$/, '$1');
    // this.$http.defaults.headers.common.Authorization = `Bearer ${this.token}`;

    //    API
    // const api = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/ec/products`;
    // this.$http
    //   .get(api)
    //   .then((res) => {
    //     console.log('AJAX ');
    //     this.products = res.data.data;
    //   })
    //   .catch((error) => {
    //     console.log('AJAX ');
    //     console.log(error.response);
    //   });
    // 
    this.getProducts();
  },
  methods: {
    //  [ok]
    getProducts(nowPage = 1) { // page 1 
      this.isLoading = true;
      const api = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/ec/products?page=${nowPage}`;
      this.$http.get(api)
        .then((res) => {
          this.isLoading = false;
          console.log(' ');
          this.products = res.data.data;
          this.pagination = res.data.meta.pagination;
        })
        .catch((error) => {
          this.isLoading = false;
          console.log('', error.response);
        });
    },
    //   Modal  [ok]
    getDetail(id) {
      const api = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/ec/product/${id}`;
      this.$http.get(api)
        .then((res) => {
          console.log(' ', res);
          this.tempProduct = res.data.data;
          $('#addModal').modal('show');
        })
        .catch((error) => {
          console.log('', error.response);
        });
    },
    //  Modal [ok]
    openModal(isNew, item) {
      switch (isNew) {
        case 'new': // 
          this.tempProduct = { // 
            imageUrl: [],
          };
          this.isNew = true;
          $('#addModal').modal('show');
          break;
        case 'edit': // 
          this.isNew = false; // 
          // 
          this.getDetail(item.id);
          break;
        case 'dele': // 
          this.tempProduct = { ...item }; // 
          $('#deleModal').modal('show');
          break;
        default:
          break;
      }
    },
    //    [ok]
    updateProduct() {
      this.isLoading = true;
      //      let 
      let api = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/ec/product`;
      // 
      let httpMethod = 'post';

      // api
      // if  AJAX  post
      if (!this.isNew) {
        api = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/ec/product/${this.tempProduct.id}`;
        httpMethod = 'patch';
      }

      this.$http[httpMethod](api, this.tempProduct)
        .then(() => {
          this.isLoading = false;
          console.log(' ');
          $('#addModal').modal('hide');
          this.$bus.$emit('message:push',
            '() ',
            'success');
          //  
          this.getProducts();
        })
        .catch((error) => {
          this.isLoading = false;
          $('#addModal').modal('hide');
          console.log('updateProduct ', error.response.data);
        });
    },
    //  [ok]
    deleProduct(id) {
      this.isLoading = true;
      const api = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/ec/product/${id}`;
      this.$http.delete(api)
        .then(() => {
          this.isLoading = false;
          //  Modal
          $('#deleModal').modal('hide');
          //  getProducts
          this.getProducts();
          this.$bus.$emit('message:push',
            '()',
            'success');
          console.log('');
        });
    },
  },
};
</script>

